/* -------------------------------------------------------------------------- */
/*                                HEADER STYLES                               */
/* -------------------------------------------------------------------------- */
.header {
  height: 8rem;
  background-color: #131313;
}
.header.open .header__toggle > span:nth-child(1) {
  transform: rotate(45deg);
  transform-origin: top left;
  background: linear-gradient(to left, #b02e0c, #eb4511);
}
.header.open .header__toggle > span:nth-child(2) {
  transform: translateX(10rem);
  opacity: 0;
  transform-origin: left center;
  transition-delay: 100ms;
}
.header.open .header__toggle > span:nth-child(3) {
  transform: rotate(-45deg);
  transform-origin: bottom left;
  background: linear-gradient(to right, #b02e0c, #eb4511);
}
.header .overlay {
  z-index: 100;
  opacity: 0;
  position: fixed;
  top: 8rem;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(to bottom, #666666, transparent);
}
.header .overlay.fade-in {
  display: block;
}
@media (min-width: 64em) {
  .header .overlay.fade-in {
    display: none;
  }
}
.header .overlay.fade-out {
  opacity: 0;
}
.header nav {
  max-width: 90vw;
  margin: 0 auto;
}
@media (max-width: 63.9375em) {
  .header nav {
    padding-top: 1.215rem;
  }
}
.header__logo img {
  height: 5rem;
  width: 100%;
}
.header__links a {
  position: relative;
  font-size: 1.8rem;
  color: #cccccc;
  transition: color 300ms ease-in-out;
}
.header__links a::before {
  content: "";
  position: absolute;
  display: block;
  height: 0.25rem;
  left: 0;
  right: 0;
  bottom: -2.8rem;
  background: #b02e0c;
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__links a:hover {
  color: #eb4511;
}
.header__links a:hover::before {
  opacity: 1;
}
.header__toggle > span {
  display: block;
  width: 28px;
  height: 3px;
  background-color: #cccccc;
  border-radius: 20%;
  transition: all 300ms ease-in-out;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 6px;
}
.header__toggle:hover > span:nth-child(1) {
  transform: rotate(3deg) scale(1.1);
}
.header__toggle:hover > span:nth-child(2) {
  transform: rotate(-3deg) scale(1.1);
}
.header__toggle:hover > span:nth-child(3) {
  transform: rotate(3deg) scale(1.1);
}

.nav {
  /* ------------------------------- NAV DESKTOP ------------------------------- */
  /* ------------------------------- NAV MOBILE ------------------------------ */
}
.nav__desktop {
  padding: 0;
  margin: 0;
}
.nav__desktop:not(:last-child) {
  margin-right: 1rem;
}
.nav__desktop > li {
  height: 8rem;
  display: inline-block;
}
.nav__desktop > li:not(:last-child) {
  margin-right: 2rem;
}
.nav__desktop > li:hover .nav__dropdownMenu {
  display: block;
  animation: grow 0.3s ease-in-out;
}
.nav__desktop > li > a {
  position: relative;
  top: 2.9rem;
}
.nav__dropdownMenu {
  position: absolute;
  z-index: 1;
  padding: 0;
  background-color: #131313;
  font-size: 1.6rem;
  width: 25rem;
  top: 8rem;
  display: none;
  line-height: 1.8;
}
.nav__dropdownMenu > li {
  position: relative;
  padding: 0 1rem;
}
.nav__dropdownMenu > li:hover .nav__dropdownSubMenu {
  display: block;
  animation: grow 0.3s ease-in-out;
}
.nav__dropdownMenu a::before {
  display: none;
}
.nav__dropdownSubMenu {
  padding: 0.5rem 1rem;
  position: absolute;
  background-color: #131313;
  width: 25rem;
  top: 0;
  left: 25rem;
  display: none;
}
.nav__cta {
  opacity: 0.75;
  transition: opacity 300ms ease-in-out;
}
.nav__cta:hover {
  border: 1px solid #cccccc;
  opacity: 1;
}
.nav__mobile {
  z-index: 150;
  position: relative;
  top: 3rem;
  margin: 0 auto;
  width: 90%;
  max-width: 50rem;
  border-radius: 1rem;
  height: 40rem;
  background-color: #131313;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  padding: 1rem;
}
@media (min-width: 64em) {
  .nav__mobile {
    display: none;
  }
}
.nav__mobile a {
  text-transform: uppercase;
  font-size: 1.8rem;
  font-weight: 400;
  color: white;
}
.nav__mobile a:hover {
  color: #b02e0c;
}

